<template>
  <div class="form-page">
    <template v-for="(option, index) of options">
      <template v-if="option.checkOption">
        <div class="col-sm-12 checkOption">
          <label class="control-label"><span v-if="option.isMust" class="MustFill">*</span>{{ option.name }}：</label>
          <div class="checkbox i-checks"  v-for="value of option.checkOption">
            <label>
              <input type="checkbox" :value="value" :name="option.key"> <i></i> {{ value }}</label>
          </div>
        </div>
      </template>


      <template v-else-if="option.isRemark">
        <div class="floor">
          <label class="col-sm-2 control-label"><span v-if="option.isMust" class="MustFill">*</span>{{ option.name }}</label>
          <div class="col-sm-10 right">
            <textarea maxlength="1000" class="form-control" v-model="form[option.key]"></textarea>
            <p><span class="remarkText">{{ option.remark }}</span></p>
          </div>
        </div>
      </template>

      <template v-else>
        <div class="col-sm-6" v-if="!option.isHide">
          <div class="form-group">
            <label class="col-sm-4 control-label"><span v-if="option.isMust" class="MustFill">*</span>{{ option.name }}</label>
            <div class="col-sm-8 right">
              <!-- readonly -->
              <template v-if="disabled">
                <p class="text">{{ (option.isFile && form[option.key]) ? api_url + form[option.key] : form[option.key] }}</p>
              </template>
              <!-- edit -->
              <template v-else>
                <template v-if="option.isDateTime">
                  <input readonly class="form-control layer-date" :id="option.key" placeholder="请选择日期" @click="showTime(option.key)">
                </template>
                <template v-else-if="option.selectOption">
                  <select class="form-control" v-model="form[option.key]">
                    <option :value="''">-- 请选择 --</option>
                    <option v-for="value of option.selectOption" :value="value">{{ value }}</option>
                  </select>
                </template>
                <template v-else-if="option.isBoolean">
                  <select class="form-control" v-model="form[option.key]">
                    <option value="是">是</option>
                    <option value="否">否</option>
                  </select>
                </template>
                <template v-else-if="option.isFile">
                  <button type="button" class="btn btn-sm btn-primary"><i class="fa fa-upload"></i>&nbsp;&nbsp;
                    <span class="bold">选择附件</span>
                  </button>
                  <input :id="option.key" type="file" class="file" @change="fileChange(option.key)">
                  <span class="remark">(最大50M)</span>
                  {{ (form[option.key] && form[option.key].name) ? form[option.key].name : form[option.key] ? api_url + form[option.key] : '' }}
                </template>
                <template v-else>
                  <input v-if="option.type == 'number'" v-model="form[option.key]" class="form-control"
                         type="text" id="test" onkeyup="this.value=this.value.replace(/[^\d\.]/g,'')" onblur="this.value=this.value.replace(/[^\d\.]/g,'')" />
                  <input v-else :type="option.type ? option.type : 'text'" class="form-control" v-model="form[option.key]">
                </template>
              </template>
              <!-- end -->
              <span class="remarkText">{{ option.remark }}</span>
            </div>
          </div>
        </div>
      </template>
    </template>

    <div class="floor" v-if="form.status == 2">
      <label class="col-sm-2 control-label">驳回原因</label>
      <div class="col-sm-10">
        <p class="text red">{{ form.reason }}</p>
      </div>
    </div>
    <div style="display: none">{{ count }}</div>
  </div>
</template>

<script type="text/ecmascript-6">
import { API_URL } from '@/assets/config'
import dataService from '@/assets/method/data'
import utilizeService from '@/assets/template/utilize'
export default {
  name: 'public-form',
  props: ['dataKeys', 'form', 'disabled'],
  data () {
    return {
      api_url: API_URL,
      options: [],
      count: 0
    }
  },
  methods: {
    showTime (elem) {
      laydate({ elem: '#' + elem })
    },
    fileChange (key) {
      if ($('#' + key).get(0).files[0]) {
        this.form[key] = $('#' + key).get(0).files[0]
        this.count++
      }
    }
  },
  mounted () {
    this.options = this.dataKeys
    for (const option of this.options) {
      if (option.isDateTime) {
        const interval = setInterval(() => {
          if (this.form[option.key] && $('#' + option.key).length > 0) {
            $('#' + option.key).val(this.form[option.key])
            clearInterval(interval)
          }
        }, 20)
      }

      if (option.checkOption) {
        const interval = setInterval(() => {
          if ($('.checkbox input[name=' + option.key + ']').length >= option.checkOption.length) {
            let arr = []
            if (this.form[option.key]) {
              arr = this.form[option.key].split(',')
              dataService.setCheckBoxVal(arr, option.key)
            }
            clearInterval(interval)
          }
        }, 20)
      }

      if (option.selectOption) {
        if (!this.form[option.key]) {
          this.form[option.key] = ''
        }
      }
    }
    setTimeout(() => utilizeService.iCheck(), 200)
  }
}
</script>

<style scoped>
  p.text {
    margin: 7px 0 0;
  }
  p.red {
    color: red;
  }
  .checkbox {
    display: inline-block;
  }
  .checkOption {
    text-align: center;
    margin-bottom: 10px;
  }
  .remarkText {
    position: absolute;
    color: #999;
  }
  .remark {
    color: #999;
  }
</style>



