import { API_URL } from '@/assets/config'
function getFormVal (dataKeys, form) {
  let result = {}
  return new Promise(resolver => {
    for (const option of dataKeys) {
      if (option.isDateTime) {
        result[option.key] = $('#' + option.key).val()
      } else if (option.isFile) {
        if (form[option.key] && form[option.key].name) {
          result[option.key] = fileUpload(form[option.key])
        } else {
          result[option.key] = form[option.key]
        }
      } else if (option.checkOption) {
        result[option.key] = getChecked(option.key).map(value => {
          return value
        }).join()
      } else {
        result[option.key] = form[option.key]
      }

      if (option.isMust && !result[option.key]) {
        result = {
          isFalse: true,
          option: option
        }
        break
      }
    }
    if (result.isFalse) {
      if (result.isFalse) {
        toastr.info(result.option.name + '不能为空')
      }
    } else {
      resolver(result)
    }
  });
}

// 文件上传,input[type=file]标签选择器$(select).get(0).files[0]
function fileUpload(file, manual = false) {
  var result;
  var fd = new FormData();
  fd.append('files', file);
  $.ajax({
    url: API_URL + (manual ? "manual" : "files"),
    type: "POST",
    processData: false,
    contentType: false,
    headers: {},
    data: fd,
    async: false,
    success: function(data) {
      result = data.data;
    },
    error: function(data) {
      toastr.error('文件上传失败')
    }
  });
  return result;
}

