function format(time, format) { // 日期格式化
  var t = new Date(time);
  var tf = function(i){return (i < 10 ? '0' : '') + i};
  return format.replace(/yyyy|MM|dd|HH|mm|ss/g, function(a){
    switch(a){
      case 'yyyy':
        return tf(t.getFullYear());
        break;
      case 'MM':
        return tf(t.getMonth() + 1);
        break;
      case 'dd':
        return tf(t.getDate());
        break;
      case 'HH':
        return tf(t.getHours());
        break;
      case 'mm':
        return tf(t.getMinutes());
        break;
      case 'ss':
        return tf(t.getSeconds());
        break;
    }
  })
}

// 添加表单验证
function validate (e) {
  return $(e).validate()
}
// 重置表单
function resetForm (validate, isClear = false) {
  validate.resetForm()
  $('.form-group').removeClass('has-success')
  $('.form-group').removeClass('has-error')
  if (isClear) {
    clearChecked()
  }
}
function setRadioVal (value, name) {
  $('.radio input[name=' + name + '][value="' + value + '"]').parent().addClass('checked')
  $('.radio input[name=' + name + '][value="' + value + '"]').prop("checked", 'checked')
}

function getRadioVal (name) {
  return $('.radio .checked input[name=' + name + ']').attr('value')
}

function getChecked (name) {
  var result = []
  $('.icheckbox_square-green.checked input[name=' + name + ']').each(function () {
    result.push($(this).val())
  })
  return result
}
function setCheckBoxVal (options, name) {
  var isNumber = true
  if (typeof options[0] == 'object') {
    isNumber = false
  }
  options.forEach(option => {
    var value = isNumber ? option : option.id
    $('.checkbox input[name=' + name + '][value="' + value + '"]').parent().addClass('checked')
    $('.checkbox input[name=' + name + '][value="' + value + '"]').prop("checked", 'checked')
  })
}
function selectAllCheckBox (name) {
  $('.checkbox input[name=' + name + ']').parent().addClass('checked')
  $('.checkbox input[name=' + name + ']').prop("checked", 'checked')
}
// 清除复选框选中状态
function clearChecked (name) {
  if (name) {
    $('.checkbox input[name=' + name + ']').parent().removeClass('checked')
    $('.checkbox input[name=' + name + ']').prop("checked", '')
    return
  }
  $('.checkbox input').each(function () {
    this.checked = false
  })
  $('.checked').removeClass('checked')
}

function download (response) {
  if (response.code == 200) {
    const url = API_URL + response.data.files
    var $form = $('<form method="GET"></form>');
    $form.attr('action', url);
    $form.appendTo($('body'));
    $form.submit();
  }
}

function tableAddKeys (dataKeys, data) {
  const result = []
  dataKeys.map((option, index) => {
    result[index] = {}
    for (const key in option) {
      result[index][key] = option[key]
    }
  })
  data.map(option => {
    const item = {
      name: option.display,
      key: option.name
    }
    if (option.type == 'number')  item['type'] = 'number'
    if (option.type == 'isDateTime')  item['isDateTime'] = 1
    if (option.can_null == 0)  item['isMust'] = 1
    result.push(item)
  })
  return result
}

function getTableKeys(Service, code, dataKeys) {
  return new Promise(resolver => {
    Service.Customize.get({ model: code }).then(response => {
      if (response.code == 200) {
        resolver(tableAddKeys(dataKeys, response.data))
      } else {
        resolver(dataKeys)
      }
    })
  })
}

export default {
  getFormVal,
  tabReload,
  format,
  validate, resetForm,
  setRadioVal, getRadioVal, getChecked, setCheckBoxVal, selectAllCheckBox, clearChecked,
  download, fileUpload,
  tableAddKeys, getTableKeys
}



