let canShow = true
function getUser (key) {
  const user = getItem('user', true)
  if (!user || !user.id) {
    if (!canShow) return
    canShow = false
    setTimeout(() => canShow = true, 2000)
    toastr.warning('获取用户数据失败, 请重新登录')
    return false
  }
  if (key) {
    return user[key]
  }
  return user
}

function setUser (data) {
  setItem('user', data, true)
}

function setToken (data) {
  setItem('token', data, true)
}

function getToken () {
  return getItem('token', true)
}

function setItem (name, data, isPermanent = false) {
  if (isPermanent) {
    localStorage.setItem(name, JSON.stringify(data))
  } else {
    sessionStorage.setItem(name, JSON.stringify(data))
  }
}

function getItem (name, isPermanent = false) {
  if (isPermanent) {
    return JSON.parse(localStorage.getItem(name))
  }
  return JSON.parse(sessionStorage.getItem(name))
}

function clear () {
  localStorage.clear()
  sessionStorage.clear()
}

function hasPermission (name) { // 是否拥有传入权限名权限,admin默认拥有
  const permissions = getUser('permission')
  if ($.inArray('admin', permissions) > -1 || $.inArray(name, permissions) > -1) {
    return true
  }
  return false
}

function isOrdinary() { // 是否普通用户
  if (getUser('permission').length == 0) {
    return true
  }
  return false
}

function pageOperation (name) { // 列表页控制权限
  const operation = {}
  operation.add = true
  operation.del = true
  if (hasPermission(name) || isOrdinary()) {
    operation.edit = true
  }
  if (hasPermission(name)) {
    operation.export = true // 导出权限
  }
  if (hasPermission('content_management')) {
    operation.approval = true // 审批权限
  }
  return operation
}

function hasDetailsOperation (name) { // 是否有详情页的修改权限
  if (hasPermission(name)) {
    return true
  }
  return true
}

function isMy (form) { // 是否属于我的
  if (getUser('username') == form.creator) {
    return true
  }
  return false
}
function canEdit (form, permission = 'admin') { // 是否可以修改
  if (
    (isMy(form) && (form.status == 2 || form.status == 4))  // 我的"未提交"或"已驳回"
    || (hasPermission(permission) && form.status != 3) // 拥有管理权限,未审批
    || (hasPermission(permission) && hasPermission('content_management') && form.status == 3) // 拥有管理权限 + 审批权限,已审批
  ){
    return true
  }
  return false
}
function canLocking (form, permission = 'admin') { // 是否可以提交
  if (
    (isMy(form) && (form.status == 2 || form.status == 4))  // 我的"未提交"或"已驳回"
  ){
    return true
  }
  return false
}

export default {
  getUser, setUser, setToken, getToken,
  setItem, getItem, clear,
  hasPermission, pageOperation, hasDetailsOperation,
  isMy, canEdit, canLocking
}



