<!-- 一个格式化的列表页, 仅为接受子组件js事件后,将事件发射到父组件,无实际操作, 具体代码查看父组件及子组件  -->
<template>
  <div>
    <public-search
      v-bind:pageName="code"
      v-bind:condition="condition"
      v-bind:searchOptions="searchOptions"
      @search="search"
    ></public-search>

    <div class="text-left">
      <button v-if="operation.add" class="btn btn-primary" @click="add()">新增</button>
      <file-export v-if="operation.export" v-bind:code="code" v-bind:condition="condition" @fileExport="fileExport"></file-export>
    </div>

    <public-table
      v-bind:operation="operation"
      v-bind:dataKeys="dataKeys"
      v-bind:items="items"
      v-bind:page="page"
      v-bind:total="total"
      v-bind:pageSize="pageSize"
      v-bind:condition="condition"
      v-bind:code="code"
      v-bind:service="service"
      @download="download"
      @edit="edit"
      @delConfirm="delConfirm"
      @pageChange="pageChange"
      @sort="search"
    ></public-table>
  </div>
</template>

<script type="text/ecmascript-6">
export default {
  name: 'format-table',
  props: ['code', 'condition', 'operation', 'service',
    'searchOptions',
    'dataKeys', 'items', 'page', 'total', 'pageSize'],
  data () {
    return {}
  },
  methods: {
    search (result) {
      this.$emit('search', result, '搜索')
    },
    fileExport (request) {
      this.$emit('fileExport', request, '导出文件')
    },
    download (item) {
      this.$emit('download', item, '下载')
    },
    edit (item) {
      this.$emit('edit', item, '编辑')
    },
    delConfirm (item) {
      this.$emit('delConfirm', item, '删除')
    },
    pageChange (page) {
      this.$emit('pageChange', page, '换页')
    },
    add () {
      this.$emit('add', {}, '新增')
    }
  },
  created () {

  },
  mounted () {

  }
}
</script>

<style scoped>

</style>



