import Vue from 'vue'
import Vuex from 'vuex'
import App from './App'
import router from './router'
import VueResource from 'vue-resource'
import vuePagination from 'vue-pagination-y'
// components
import VJstree from 'vue-jstree'
import Delete from './components/shared/delete'
import PublicForm from './components/shared/public-form'
import PublicSearch from './components/shared/public-search'
import PublicTable from './components/shared/public-table'
import FileExport from './components/shared/file-export'
import EditForm from './components/shared/edit-form'
import FormatTable from './components/shared/format-table'

import filterService from '@/assets/method/filter'


Vue.config.productionTip = false
Vue.use(Vuex)
Vue.use(VueResource)
Vue.use(vuePagination)

Vue.component('v-jstree', VJstree)
Vue.component('delete', Delete)
Vue.component('public-form', PublicForm)
Vue.component('public-search', PublicSearch)
Vue.component('public-table', PublicTable)
Vue.component('file-export', FileExport)
Vue.component('edit-form', EditForm)
Vue.component('format-table', FormatTable)


// defined filter for global
for (let transform in filterService) {
  Vue.filter(transform, filterService[transform])
}

const store = new Vuex.Store({
  state: {
    count: 0
  },
  mutations: {
    increment (state, value) {
      state.count = value
    }
  }
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  store,
  router,
  components: { App },
  template: '<App/>'
})



