<template>
  <div>
    <h3>{{ formTitle }}
      <template v-if="hasOperation">
        <a v-if="!isEdit" @click="edit()">编辑</a>
        <a v-if="isEdit" @click="confirm()">保存</a>
        <a class="cancel" v-if="isEdit" @click="cancel()">取消</a>
      </template>
    </h3>
    <hr>
    <div v-if="!isEdit" class="text">
      <div class="row">
        <div class="col-sm-6" v-for="option of keys">
          <p><label class="control-label">{{ option.label }}</label>：{{ data[option.key] }}</p>
        </div>
      </div>
    </div>

    <div v-if="isEdit">
      <div class="row">
        <div class="col-sm-6" v-for="option of keys">
          <div class="option">
            <label class="control-label">{{ option.label }}</label>
            <input type="text" class="form-control" v-model="form[option.key]">
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    name: 'edit-form',
    props: ['formTitle', 'data', 'keys', 'hasOperation'],
    data () {
      return {
        form: {},
        isEdit: false
      }
    },
    methods: {
      edit () {
        this.keys.map(option => {
          this.form[option.key] = this.data[option.key]
        })
        this.isEdit = true
      },
      cancel () {
        this.clear()
      },
      confirm () {
        this.$emit('confirm', this.form, '确认保存')
        this.clear()
      },
      clear () {
        this.form = {}
        this.isEdit = false
      }
    }
  }
</script>

<style scoped>
  a {
    float: right;
    margin-left: 20px;
    font-size: 14px;
  }
  a.cancel {
    color: #999;
  }
  hr {
    margin: 0 0 10px 0;
  }
  .text {
    margin-top: 35px;
    padding-left: 60px;
  }
  .option {
    display: inline-block;
    width: 100%;
    margin-bottom: 10px;
  }
  .option label {
    display: inline-block;
    width: 100px;
    float: left;
    margin: 8px 15px 0 15px;
    text-align: right;
  }
  .option input {
    display: inline-block;
    width: 200px;
    float: left;
  }
</style>



