  height: 40px;
  line-height: 40px;
  cursor: pointer;
  color: #333;
  font-size: 16px;
  text-align: center;
}
.tag li.active {
  color: #fff;
  background-color: #f60;
}
img.icon {
  display: block;
  width: 50px;
  height: 30px;
  margin: 0 auto;
  background-size: 100%;
}

.MustFill {
  display: inline-block;
  color: red;
  font-size: 15px;
  line-height: 15px;
}
.floor {
  display: inline-block;
  width: 100%;
}
.form-page .floor {
  margin-bottom: 5px;
}
</style>



