<template>
  <div class="table-page">
    <div class="searchOption">
      <label class="control-label">每页展示条数：</label>
      <input type="number" class="form-control" v-model="condition.page_num" onkeyup="this.value=this.value.replace(/[^0-9a-zA-Z]/g,'')" @change="pageSizeChange()">
    </div>
    <table class="table table-bordered text-center">
      <thead>
      <tr>
        <th v-if="canCheck && operation && operation.export"></th>
        <th v-for="option of dataKeys" v-if="option.isHide != 2 && !option.hideTable" class="sort" :class="{ remark: option.isRemark }" @click="sort(option.key)">
          <div class="sortText">{{ option.name }}
            <template v-if="sortKey == option.key">
              <div class="sortIcon desc" v-if="sortType == 'desc'">
                <i class="fa fa-sort-desc" aria-hidden="true"></i>
              </div>
              <div class="sortIcon" v-else>
                <i class="fa fa-sort-asc" aria-hidden="true"></i>
              </div>
            </template>
            <template v-else>
              <div class="sortIconOption">
                <i class="fa fa-sort-asc" aria-hidden="true"></i>
                <i class="fa fa-sort-desc" aria-hidden="true"></i>
              </div>
            </template>
          </div>
        </th>
        <th class="sort" @click="sort('status')">
          <div class="sortText">状态
            <template v-if="sortKey == 'status'">
              <div class="sortIcon desc" v-if="sortType == 'desc'">
                <i class="fa fa-sort-desc" aria-hidden="true"></i>
              </div>
              <div class="sortIcon" v-else>
                <i class="fa fa-sort-asc" aria-hidden="true"></i>
              </div>
            </template>
            <template v-else>
              <div class="sortIconOption">
                <i class="fa fa-sort-asc" aria-hidden="true"></i>
                <i class="fa fa-sort-desc" aria-hidden="true"></i>
              </div>
            </template>
          </div>
        </th>
        <th>操作</th>
      </tr>
      </thead>
      <tbody>
      <tr v-for="(item, index) of items" :key="item.id">
        <td v-if="canCheck && operation && operation.export">
          <div class="checkbox i-checks">
            <label><input name="selectItems" type="checkbox" :value="item.id"><i></i></label>
          </div>
        </td>
        <td v-for="option of dataKeys" v-if="option.isHide != 2 && !option.hideTable">
          <template v-if="option.isFile">
            <template v-if="item[option.key]">
              <!--<button v-if="option.isFile == 1" class="btn btn-sm btn-primary" @click="download(item)">下载</button>-->
              <template v-if="option.isFile == 1">有</template>
              <img v-if="option.isFile == 2" :src="api + item[option.key]" class="icon">
            </template>
            <template v-else>无</template>
          </template>
          <template v-else>
            {{ item[option.key] }}
          </template>
        </td>
        <td>{{ item.status | approvalStatus }}</td>
        <td style="min-width: 120px">
          <button v-if="!operation || operation.edit" class="btn btn-sm btn-primary" @click="edit(item)">编辑</button>
          <button v-else class="btn btn-sm btn-primary" @click="edit(item)">详情</button>
          <delete v-if="!operation || operation.del" @delConfirm="delConfirm" v-bind:item="item"></delete>
          <template v-if="canApproval(item)">
            <button class="btn btn-sm btn-warning" @click="agree(item)">同意</button>
            <button class="btn btn-sm btn-danger" @click="refuse(item)">驳回</button>
          </template>
        </td>
      </tr>
      </tbody>
    </table>
    <vue-pagination :current-page="page" :total="total" :page-size="pageSize" @current-change="pageChange" theme="gray"></vue-pagination>
    <div style="display: none">{{ count }}</div>
  </div>
</template>

<script type="text/ecmascript-6">
import { API_URL } from '@/assets/config'
export default {
  name: 'public-table',
  props: ['dataKeys', 'items', 'page', 'total', 'pageSize', 'unCheck', 'operation', 'condition', 'code', 'service'],
  data () {
    return {
      api: API_URL,
      options: [],
      count: 0,
      canCheck: true,
      sortKey: 'id',
      sortType: 'asc' // 排序 升序:asc 倒序:desc
    }
  },
  methods: {
    pageSizeChange () {
      const size = Number(this.condition.page_num)
      if (size < 1) {
        this.condition.page_num = 1
      } else if (size > 100) {
        this.condition.page_num = 100
      } else {
        this.condition.page_num = size
      }
    },
    download (item) {
      this.$emit('download', item, '下载')
    },
    edit (item) {
      this.$emit('edit', item, '编辑')
    },
    delConfirm (item) {
      this.$emit('delConfirm', item, '删除')
    },
    pageChange (page) {
      this.$emit('pageChange', page, '换页')
    },
    sort (key) {
      if (key == this.sortKey) {
        this.sortType = this.sortType == 'asc' ? 'desc' : 'asc'
      }
      this.sortKey = key

      if (this.sortType == 'asc') { // 正序
        this.condition['orderBy'] = this.sortKey
        this.condition['orderByDesc'] = null
      } else {
        this.condition['orderBy'] = null
        this.condition['orderByDesc'] = this.sortKey
      }
      this.$emit('sort', this.condition, '搜索')
    },
    canApproval (item) {
      if (this.operation && this.operation.approval && item.status == 1) {
        return true
      }
      return false
    },
    agree (item) {
      swal({
        title: '确定审核通过吗',
        type: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#DD6B55',
        confirmButtonText: '确认',
        cancelButtonText: '取消',
        closeOnConfirm: false
      },() => {
        this.service.Approval.agree(item.id, { type: this.code }).then(response => {
          if (response.code == 200) {
            swal('审核成功！', '',  'success')
            this.$emit('sort', this.condition, '搜索')
          }
        })
      })
    },
    refuse (item) {
      const reason = prompt("确定审核驳回吗?");
      const isRefuse = reason != null
      if (!isRefuse) { // 如果取消驳回
        return
      }
      this.service.Approval.refuse(item.id, {
        type: this.code,
        detail: reason
      }).then(response => {
        if (response.code == 200) {
          swal('驳回成功！', '',  'error')
          this.$emit('sort', this.condition, '搜索')
        }
      })
    }
  },
  mounted () {
    if (this.unCheck) {
      this.canCheck = false
    }
  }
}
</script>

<style scoped>
  .icon {
    display: block;
    width: 50px;
    height: 30px;
    margin: 0 auto;
    background-size: 100%;
  }
  .searchOption {
    margin: 10px 0;
  }
  .searchOption * {
    display: inline-block;
  }
  .searchOption input {
    width: 100px;
  }
  .remark {
    min-width: 200px;
  }
  .table td {
    text-align: center;
    max-width: 200px;
    height: 20px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
</style>



