function addTab (url, index, name) {
  var dataUrl = url
  var dataIndex = index
  var menuName = name
  var flag = true

  // 选项卡菜单已存在
  $('.J_menuTab', window.parent.document).each(function () {
    if ($(this).data('id') == dataUrl) {
      if (!$(this).hasClass('active')) {
        $(this).addClass('active').siblings('.J_menuTab').removeClass('active')
        scrollToTab(this)
        // 显示tab对应的内容区
        $('.J_mainContent .J_iframe', window.parent.document).each(function () {
          if ($(this).data('id') == dataUrl) {
            $(this).show().siblings('.J_iframe').hide()
            return false
          }
        })
      }
      flag = false
      return false
    }
  })

  // 选项卡菜单不存在
  if (flag) {
    var str = '<a href="javascript:;" class="active J_menuTab" data-id="' + dataUrl + '">' + menuName + ' <i class="fa fa-times-circle"></i></a>'
    $('.J_menuTab', window.parent.document).removeClass('active')

    // 添加选项卡对应的iframe
    var str1 = '<iframe class="J_iframe" name="iframe' + dataIndex + '" width="100%" height="100%" src="' + dataUrl + '" frameborder="0" data-id="' + dataUrl + '" seamless></iframe>'
    $('.J_mainContent', window.parent.document).find('iframe.J_iframe').hide().parents('.J_mainContent').append(str1)

    $('.J_menuTabs .page-tabs-content', window.parent.document).append(str)
    scrollToTab($('.J_menuTab.active', window.parent.document))
  }
  return false
}

import { TAB_NAME } from '../config'
function addDetailsTab (url, name) {  // 一个共用的详情页
  // 如果已经存在该详情页,删除它
  if (window.parent.document.all[TAB_NAME]) {
    const tag = window.parent.document.all[TAB_NAME]
    $(tag).remove()
    $('.J_menuTab[data-name="' + TAB_NAME + '"]', window.parent.document).remove()
  }

  // 打开详情页
  var dataUrl = url
  var menuName = name
  var flag = true

  // 选项卡菜单已存在
  $('.J_menuTab', window.parent.document).each(function () {
    if ($(this).data('id') == dataUrl) {
      if (!$(this).hasClass('active')) {
        $(this).addClass('active').siblings('.J_menuTab').removeClass('active')
        scrollToTab(this)
        // 显示tab对应的内容区
        $('.J_mainContent .J_iframe', window.parent.document).each(function () {
          if ($(this).data('id') == dataUrl) {
            $(this).show().siblings('.J_iframe').hide()
            return false
          }
        })
      }
      flag = false
      return false
    }
  })

  // 选项卡菜单不存在
  if (flag) {
    var str = '<a href="javascript:;" class="active J_menuTab" data-id="' + dataUrl + '" data-name="' + TAB_NAME + '">' + menuName + ' <i class="fa fa-times-circle"></i></a>'
    $('.J_menuTab', window.parent.document).removeClass('active')

    // 添加选项卡对应的iframe
    var str1 = '<iframe class="J_iframe" name="' + TAB_NAME + '" width="100%" height="100%" src="' + dataUrl + '" frameborder="0" data-id="' + dataUrl + '" seamless></iframe>'
    $('.J_mainContent', window.parent.document).find('iframe.J_iframe').hide().parents('.J_mainContent').append(str1)

    $('.J_menuTabs .page-tabs-content', window.parent.document).append(str)
    scrollToTab($('.J_menuTab.active', window.parent.document))
  }
  return false
}

function scrollToTab(element) {
  var marginLeftVal = calSumWidth($(element).prevAll()), marginRightVal = calSumWidth($(element).nextAll())
  // 可视区域非tab宽度
  var tabOuterWidth = calSumWidth($('.content-tabs', window.parent.document).children().not('.J_menuTabs'))
  //可视区域tab宽度
  var visibleWidth = $('.content-tabs', window.parent.document).outerWidth(true) - tabOuterWidth
  //实际滚动宽度
  var scrollVal = 0
  if ($('.page-tabs-content', window.parent.document).outerWidth() < visibleWidth) {
    scrollVal = 0
  } else if (marginRightVal <= (visibleWidth - $(element).outerWidth(true) - $(element).next().outerWidth(true))) {
    if ((visibleWidth - $(element).next().outerWidth(true)) > marginRightVal) {
      scrollVal = marginLeftVal
      var tabElement = element
      while ((scrollVal - $(tabElement).outerWidth()) > ($('.page-tabs-content', window.parent.document).outerWidth() - visibleWidth)) {
        scrollVal -= $(tabElement).prev().outerWidth()
        tabElement = $(tabElement).prev()
      }
    }

  } else if (marginLeftVal > (visibleWidth - $(element).outerWidth(true) - $(element).prev().outerWidth(true))) {
    scrollVal = marginLeftVal - $(element).prev().outerWidth(true)
  }
  $('.page-tabs-content', window.parent.document).animate({
    marginLeft: 0 - scrollVal + 'px'
  }, 'fast')

}

function calSumWidth (elements) {
  var width = 0
  $(elements).each(function () {
    width += $(this).outerWidth(true)
  })
  return width
}


function closeNowTab () {
  const dataId = $('.J_menuTab.active', window.parent.document).attr('data-id')
  //将当前显示的iframe标记为删除项
  $('.J_menuTab.active', window.parent.document).addClass('delActive')
  // 为删除的前一项添加选中样式
  $('.J_menuTab.delActive', window.parent.document).prev('.J_menuTab').addClass('active')
  // 显示新的iframe
  $('.J_mainContent', window.parent.document).find('iframe.J_iframe[data-id="' + $('.J_menuTab.active', window.parent.document).attr('data-id') + '"]').show()
  // 删除当前iframe
  $('.J_menuTab.delActive', window.parent.document).remove()
  $('.J_mainContent', window.parent.document).find('iframe.J_iframe[data-id="' + dataId + '"]').remove()
}

export default { addTab, closeNowTab, addDetailsTab }



