<template>
  <div style="display: inline-block">
    <button class="btn btn-info" @click="selectAll()">全选</button>
    <button class="btn btn-danger" @click="cancelAll()">全不选</button>
    <input type="text" class="form-control" v-model="name" placeholder="请输入导出文件名称">
    <button class="btn btn-info" @click="confirm()">导出</button>
    <button class="btn btn-info" @click="confirmAll()">全部导出</button>
  </div>
</template>

<script type="text/ecmascript-6">
import dataService from '@/assets/method/data'
export default {
  name: 'file-export',
  props: ['code', 'condition'],
  data () {
    return {
      name: null
    }
  },
  methods: {
    selectAll () {
      dataService.selectAllCheckBox('selectItems')
    },
    cancelAll () {
      dataService.clearChecked('selectItems')
    },
    getCondition () {
      const condition = {
        model: this.code,
        zip_name: this.name
      }
      for (const key in this.condition) {
        if (this.condition[key] && key != 'page_num') {
          condition[key] = this.condition[key]
        }
      }
      if (!this.condition.orderBy && !this.condition.orderByDesc) {
        condition['orderBy'] = 'id'
      }
      return condition
    },
    confirm () {
      const ids = dataService.getChecked('selectItems')
      if (ids.length == 0) {
        toastr.info('请勾选导出项')
        return
      }
      if (!this.name) {
        toastr.info('请输入导出文件名')
        return
      }
      const request = this.getCondition()
      request['ids'] = ids
      this.$emit('fileExport', request, '导出文件')
    },
    confirmAll () {
      if (!this.name) {
        toastr.info('请输入导出文件名')
        return
      }
      const request = this.getCondition()
      this.$emit('fileExport', request, '导出文件')
    }
  }
}
</script>

<style scoped>
  input {
    display: inline-block;
    width: 150px;
  }
</style>



