<template>
  <div class="row">
    <form @submit.prevent="search" v-show="showPage">
      <div class="col-sm-12 searchCondition" v-if="!hideStatus">
        <div class="searchOption">
          <label class="control-label">状态</label>
          <select class="form-control" v-model="status">
            <option value="0">全部</option>
            <option value="1">未审批</option>
            <option value="2">已驳回</option>
            <option value="3">审核成功</option>
          </select>
        </div>
      </div>
      <div class="col-sm-12 searchCondition">
        <template v-for="(searchOption, searchIndex) of searchOptions">
          <!-- 特殊格式 -->
          <template v-if="searchOption.type">
            <!-- 日期 -->
            <div class="dateOption" v-if="searchOption.type == 'date'">
              <label class="timeLabel">{{ searchOption.label }}</label>
              <template v-for="(option, index) of options">
                <div class="radio i-checks">
                  <label>
                    <input :name="name + searchIndex" type="radio" :value="option.value"><i></i>{{ option.name }}</label>
                </div>
              </template>
              <template v-if="searchOption.showTime">
                <input readonly class="form-control layer-date startTime" :id="name + 'Start-' + searchIndex" placeholder="开始时间" @click="showTime(name + 'Start-' + searchIndex)">
                <input readonly class="form-control layer-date endTime" :id="name + 'End-' + searchIndex" placeholder="结束时间" @click="showTime(name + 'End-' + searchIndex)">
              </template>
            </div>
            <!-- 日期 end -->
          </template>
          <!-- 下拉选项 -->
          <template v-else-if="searchOption.selectOption">
            <div class="searchOption" v-if="canShow(searchOption)">
              <label class="control-label">{{ searchOption.label }}</label>
              <select class="form-control" v-model="condition[searchOption.key]">
                <option :value="''">全部</option>
                <option v-for="option of searchOption.selectOption" :value="option">{{ option }}</option>
              </select>
            </div>
          </template>

          <!-- 普通表单 -->
          <template v-else>
            <div class="searchOption" v-if="canShow(searchOption)">
              <label class="control-label">{{ searchOption.label }}</label>
              <input type="text" class="form-control" v-model="condition[searchOption.key]">
            </div>
          </template>
        </template>
      </div>
      <div class="text-center searchBox" v-if="DefaultNumber < 0">
        <button v-if="!showAll" type="button" class="btn btn-sm btn-info" @click="all(true)">更多筛选</button>
        <button v-if="showAll" type="button" class="btn btn-sm btn-while" @click="all(false)">收起筛选</button>
      </div>
      <hr>

      <div class="text-center searchBox">
        <button type="button" class="btn btn-while search" v-if="canHide" @click="showPage = false">隐藏组件</button>
        <button type="submit" class="btn btn-warning search">查询</button>
      </div>
    </form>

    <div v-show="!showPage" class="text-center">
      <button type="button" class="btn btn-sm btn-warning" @click="showPage = true">显示搜索组件</button>
    </div>

    <div style="display: none">{{ count }}</div>
  </div>
</template>

<script type="text/ecmascript-6">
/**
 * props 说明
 *    condition
 *      type: obj{} 提交返回的数据对象
 *      示例: condition = {}
 *    searchOptions
 *      type: array[] 搜索选项
 *      array每个元素为格式obj
 *        必填项: label, key, label为显示文本, key的值为存放到condition的属性
 *        可选项: type, 格式, 默认为input[type="text"]
 *                type='date', html渲染为一组时间选择器(开始时间&结束结束),返回['2018-01-01', '2018-12-12']格式
 *
 *      示例: 输入condition = {}, searchOptions = [{ label: '名称', key: 'name' }]
 *            返回 condition = { name：'这是一个名称' }
 *     pageName
 *        type: string 唯一标识,当同一页面多次引入该组件,不能出现相同
 *     hideStatus
 *        type: boolean, true则隐藏审批状态筛选条件, 默认false
**/
import dataService from '@/assets/method/data'
import utilizeService from '@/assets/template/utilize'
export default {
  name: 'public-search',
  props: ['condition', 'searchOptions', 'pageName', 'hideStatus'],
  data () {
    return {
      name: '',
      status: this.$route.query['key'] ? 3 : 0,
      count: 0,
      options: [  // 日期单选固定项
        { name: '全部', value: -1 },
        { name: '近三天', value: 3 },
        { name: '近一周', value: 7 },
        { name: '近一个月', value: 30 },
        { name: '近半年', value: 180 },
        { name: '自定义', value: 0 }
      ],
      DefaultNumber: 3, // 默认显示筛选条件数量(状态&时间 默认显示,不计数量)
      showAll: false,  // 是否显示所有搜索条件
      showPage: false,  // 是否显示搜索组件
      canHide: false // 是否显示组件显示/隐藏按钮
    }
  },
  methods: {
    search () {
      const result = {}
      for (const index in this.searchOptions) {
        const option = this.searchOptions[index]
        if (option.selectOption) {
          result[option.key] = this.condition[option.key] ? this.condition[option.key] : ''
        } else if (option.type) {
          switch (option.type) {
            case 'date': // 日期
              const n = dataService.getRadioVal(this.name + index)
              // 如果选择"自定义", 必须填写开始时间&结束时间
              if (n == 0) {
                const startTime = $('#' + this.name + 'Start-' + index).val()
                const endTime = $('#' + this.name + 'End-' + index).val()
                if (!startTime || !endTime) {
                  toastr.warning('请选择' + option.label)
                  return
                }
                if (new Date(startTime).getTime() >= new Date(endTime).getTime()) {
                  toastr.warning('结束时间必须在开始时间之后')
                  return
                }
              }
              if (n == -1) {
                result[option.key] = null
              }
              if (n == 0) {
                result[option.key] = [
                  $('#' + this.name + 'Start-' + index).val(),
                  $('#' + this.name + 'End-' + index).val()
                ]
              }
              if (n > 0) {
                result[option.key] = [
                  dataService.format(new Date(new Date().getTime() - n*24*60*60*1000), 'yyyy-MM-dd'),
                  dataService.format(new Date(new Date().getTime() + 24*60*60*1000), 'yyyy-MM-dd')
                ]
              }
              break
          }
        } else {
          result[option.key] = this.condition[option.key]
        }
      }
      result['status'] = this.status > 0 ? this.status : null
      this.$emit('search', result, '搜索')
    },
    showTime (elem) {
      laydate({ elem: '#' + elem })
    },
    all (option) {
      this.showAll = option
    },
    canShow (option) {
      if (option.canShow || this.showAll) {
        return true
      }
      return false
    }
  },
  mounted () {
    this.count++
    this.name = this.pageName ? this.pageName : 'time'
    utilizeService.iCheck()
    if (this.searchOptions.length < 3) {
      this.showPage = true
    } else {
      this.canHide = true
    }
    this.searchOptions.map((option, index) => {
      this.condition[option.key] = '' // 未知的引起input输入被无限清空
      if (option.type == 'date') {
        setInterval(() => {
          if (!dataService.getRadioVal(this.name + index)) {
            dataService.setRadioVal(-1, this.name + index)
          }
          option.showTime = dataService.getRadioVal(this.name + index) == 0 ? true : false
          this.count++
        }, 50)
      } else if (this.DefaultNumber > 0) {
        option.canShow = true
        this.DefaultNumber--
      } else {
        this.DefaultNumber--
      }
      if (option.selectOption) {
        this.condition[option.key] = ''
      }
    })
  }
}
</script>

<style scoped>
  .i-checks {
    display: inline-block;
    float: left;
    margin-top: 8px;
  }
  .startTime, .endTime {
    margin-left: 15px;
  }
  .timeLabel {
    display: inline-block;
    float: left;
    margin: 8px 0 0 15px;
  }
  .searchCondition .searchOption {
    display: inline-block;
    float: left;
    margin-bottom: 10px;
  }
  .searchCondition label {
    display: inline-block;
    float: left;
    margin: 8px 15px 0 15px;
  }
  .searchCondition input, .searchCondition select {
    display: inline-block;
    float: left;
    width: 150px;
  }
  .dateOption {
    display: inline-block;
    width: 100%;
    margin-top: 5px;
  }
  .dateOption label {
    margin: 0;
  }
  .dateOption .timeLabel {
    margin: 8px 0 0 15px;
  }
  button.search {
    margin-top: 5px;
  }
  .searchBox {
    width: 100%;
  }
  .row {
    max-width: 1200px;
  }
  hr {
    margin: 0;
  }
</style>



