// 审批状态
function approvalStatus (value) {
  let status = ''
  switch (Number(value)) {
    case 1: status = '待审批'; break;
    case 2: status = '已驳回'; break;
    case 3: status = '审核成功'; break;
    case 4: status = '未提交'; break;
  }
  return status
}

function noticeType (type) {
  let result = ''
  switch (Number(type)) {
    case 1: result = '学校通知'; break;
    case 2: result = '部处通知'; break;
    case 3: result = '校内发文'; break;
  }
  return result
}

import { CODE } from '../constant'
function codeName (type) {
  let name
  switch (type) {
    case CODE.Researcher:
      name = '科研人员'
      break
    case CODE.TalentPlan:
      name = '人才计划'
      break
    case CODE.AcademicCommittee:
      name = '学术委员会'
      break
    case CODE.EthicsCommittee:
      name = '伦理委员会'
      break
    case CODE.CollegeArea:
      name = '实验场地'
      break
    case CODE.CollegeDevice:
      name = '研究设备'
      break
    // 学术成果
    case CODE.AcademicWorks:
      name = '学术著作'
      break
    case CODE.PeriodicalPaperChinese:
      name = '期刊论文-中文'
      break
    case CODE.PeriodicalPaperEnglish:
      name = '期刊论文-英文'
      break
    case CODE.ResearchProjectsCountry:
      name = '科研项目(国家级项目)'
      break
    case CODE.ResearchProjectsProvince:
      name = '科研项目(省部级项目)'
      break
    case CODE.ResearchProjectsOther:
      name = '科研项目(其他项目)'
      break
    case CODE.ResearchProjectsLandscape:
      name = '科研项目(横向项目)'
      break
    case CODE.ResearchAwards:
      name = '科研获奖'
      break
    case CODE.Patent:
      name = '发明专利'
      break
    case CODE.MeetingPaper:
      name = '会议论文'
      break
    case CODE.ResearchReport:
      name = '研究报告'
      break
    case CODE.HostMeeting:
      name = '主办会议/培训'
      break
    case CODE.ParticipateMeeting:
      name = '参与会议/培训'
      break
    case CODE.Score:
      name = '评分'
      break
    // 人才管理
    case CODE.MasterTraining:
      name = '硕士培养'
      break
    case CODE.DoctorTraining:
      name = '博士培养'
      break
    case CODE.TeacherAward:
      name = '教学获奖'
      break
    case CODE.GoodCourse:
      name = '精品课程'
      break
    case CODE.TeachingMaterialConstruction:
      name = '教材建设'
      break
    case CODE.StudentAwards:
      name = '学生获奖情况'
      break
    case CODE.StudentWorks:
      name = '学生发表著作情况'
      break
    case CODE.StudentMeeting:
      name = '学生境内/境外参加会议'
      break
    case CODE.StudentStudy:
      name = '学生境内/境外交流学习情况'
      break
    case CODE.StudentMeetingPaper:
      name = '学生发表会议论文'
      break
    case CODE.PracticeBase:
      name = '实践基地'
      break
    case CODE.PartTimeTutor:
      name = '兼职导师'
      break
    case CODE.ParticipateProfessionalPractice:
      name = '参加专业实践活动'
      break
    case CODE.GraduateEnrollment:
      name = '招生与毕业情况'
      break
    case CODE.RulesRegulations:
      name = '规则制度'
      break
  }
  return name
}

let transform = {
  approvalStatus: approvalStatus,
  noticeType: noticeType,
  codeName: codeName
}
export default transform



