<template>
  <button type="button" class="btn btn-danger" :class="[size ? '' : 'btn-sm']" @click="delConfirm()">{{ text ? text : '删除' }}</button>
</template>

<script>
export default {
  name: 'Delete',
  props: ['item', 'text', 'size'],
  data () {
    return {}
  },
  methods: {
    delConfirm: function () {
      let that = this
      swal({
        title: '您确定删除吗',
        text: '删除后将无法恢复，请谨慎操作！',
        type: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#DD6B55',
        confirmButtonText: '删除',
        cancelButtonText: '取消',
        closeOnConfirm: false
      }, function () {
        that.$emit('delConfirm', that.item, '确认删除')
      })
    }
  }
}
</script>



